@font-face {
  font-family: "Manrope";
  src: local("Manrope"),
    url("https://fonts.cdnfonts.com/s/16243/manrope-regular.woff")
      format("woff");
}

#main-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: Manrope;
}
h5 {
  margin-top: 0px;
  margin-bottom: 0px;
}
#content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.card-container {
  background-color: pink;
  margin: 4px;
  border-radius: 5px;
  overflow: hidden;
}
.memory-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
}
.score {
  text-align: center;
  margin-top: 20px;
  font-size: 32px;
  margin-bottom: 20px;
}
img {
  object-fit: cover;
}
button {
  margin-top: 20px;
  height: 60px;
  padding: 0 20px;
  font-size: x-large;
  color: white;
  border-radius: 8px;
  border: none;
  background-color: #01a0c6;
  cursor: pointer;
}
button:hover {
  border: 2px pink solid;
}
